.App{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100% ;
}


header {
  color: #555;
}

footer{
  background-color: rgba(0,0,0,.6);
}


.not-selected {
	user-select: none;
}

/* CSS ONLY MASONRY LAYOUT WITH "CSS MULTIPLE COLUMN LAYOUT" => https://codepen.io/xtinadelamo/pen/JRwWEd */
.stories-container{
   column-count: 3;
   column-gap: 40px;
   column-count: 1;
   column-rule-color: #ccc;
   column-rule-style: dotted;
   column-rule-width:  2px;
   border: 2px dashed #ddd;
   padding:  15px 20px;
   border-radius: 5px;

   /* From https://css.glass */
background: rgba(255, 255, 255, 0.2);
border-radius: 16px;
box-shadow: 0 4px 50px rgba(0, 0, 0, 0.2);
backdrop-filter: blur(1px);
-webkit-backdrop-filter: blur(5px);
border: 1px solid rgba(255, 255, 255, 0.3);
  }


 /* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) {
    .stories-container{ column-count: 2; }
}

 /* Large devices (desktops, 992px and up) */
/* @media (min-width: 992px) { ... } */

 /* X-Large devices (large desktops, 1200px and up) */
@media (min-width: 1200px) {
    .stories-container{ column-count: 3; }
 }

 /* XX-Large devices (larger desktops, 1400px and up) */
@media (min-width: 1400px) {
    .stories-container{ column-count: 4; }
 }



  .stories-container > li {
   display: inline-block; /* 1 - VER COMENTARIO ABAJO*/
    width: 100%; /* 2 - VER COMENTARIO ABAJO*/
    margin-top: 15px;
    margin-bottom: 15px;
    /* box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.18); */
    /* border-radius: 3px; */
    transition: all .3s;
  }

 .stories-container > li:hover{
   transform: scale(1.03);
   border-color: #ffc107 !important;
 }


